.bx-filter {
	border-radius: 4px;
	background-color: #ffffff;
	border: 1px solid rgba($color-main2, 0.3);
	padding: 11px 0 16px;
}
.smartfilter__box {
	display: flex;
	flex-wrap: wrap;
	padding: 0 23px 16px;
	border-bottom: 1px solid rgba($color-main2, 0.3);
}
.bx-filter-parameters-box {
	width: 16.66%;
	padding: 10px 0;
	margin-bottom: 10px;
	position: relative;
	cursor: pointer;
}
.bx-filter-block {
	display: none;
	position: absolute;
	top: 30px;
	left: 0;
	border-radius: 4px;
	box-shadow: 0 4px 18px 0 rgba(217, 211, 189, 0.49);
	background-color: $color-white;
	z-index: 5;
	padding: 28px 24px ;
	min-width: 408px;
	&::before {
		content: '';
		position: absolute;
		width: 100%;
		height: 1px;
		bottom: 85px;
		left: 0;
		background-color: rgba($color-main2, 0.3);
	}
}
.bx-filter-parameters-box:nth-child(6n+6) .bx-filter-block{
	left: auto;
	right: 0;
}
.bx-filter-parameters-box:nth-child(5) .bx-filter-block{
	left: auto;
	right: 0;
}
.bx-filter-parameters-box:nth-child(11) .bx-filter-block{
	left: auto;
	right: 0;
}
.bx-filter-button-box {
	padding: 11px 23px 0;
}
.bx-filter-parameters-box-title {
	display: flex;
	align-items: center;
	span {
		margin-right: 8px;
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.bx-filter-btn {
	width: 108px;
	height: 32px;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0;
	margin-top: 25px;
}
.bx-filter-param-text {
	color: $color-main2;
	font-size: 15px;
	line-height: 1.33;
}
.bx-filter-box {
	display: flex;
	flex-direction: column;
	padding-bottom: 15px;
	max-height: 200px;
	overflow-y: scroll;
}
.bx-filter-param-label {
	margin-bottom: 13px;
}
.bx-filter-parameters-box-title--active {
	span {
		color: $color-blue5;
		border-color: rgba($color-blue5, 0.3);
	}
	&:hover {
		span {
			color: $color-blue5;
			border-color: rgba($color-blue5, 0.6);
		}
	}
}
.bx-filter-box--price {
	flex-direction: row;
	label {
		margin-right: 24px;
	}
	span {
		font-size: 15px;
		line-height: 1.33;
		color: $color-main2;
		margin-right: 14px;
	}
	input {
		width: 74px;
		border-radius: 2px;
		border: 1px solid rgba($color-main2, 0.3);
		text-align: center;
		padding: 2px 2px;
		font-size: 15px;
	}
}