.pagination {
	text-align: center;
	&--hidden {
		display: none;
	}
	&__btn {
		margin: 0 auto 30px;
		width: 525px;
		height: 56px;
		border-radius: 4px;
		box-shadow: 0 2px 4px 0 rgba($color-txt1, 0.28);
		background-color: $color-blue5e;
		font-family: $font-firaM;
		font-size: 17px;
		font-weight: 500;
		display: flex;
		justify-content: center;
		align-items: center;
		color: $color-white;
		cursor: pointer;
		transition: background-color .3s;
		&:hover {
			background-color: $color-blue-hov;
		}
	}
	&__nav {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__link {
		display: flex;
		width: 24px;
		height: 24px;
		justify-content: center;
		align-items: center;
		font-size: 14px;
		color: $color-blue5e;
		border-radius: 50%;
		&:hover {
			color: $color-blue-hov;
		}
		&--active {
			background-color: $color-dark-bg;
			color: $color-main2;
		}
	}
	&__dot {
		display: flex;
		width: 24px;
		height: 24px;
		justify-content: center;
		margin-top: 3px;
	}
	&__next {
		margin-left: 8px;
	}
	&__prev {
		margin-right: 8px;
	}
}