.offers {
	margin-top: 23px;
	&__list {
		display: flex;
		width: 100%;
		flex-flow: wrap;
		overflow: hidden;
		max-height: 28px;
		&--active {
			transition: max-height .6s;
			max-height: 250px;
		}
	}
	&__link {
		border-radius: 12px;
		background-color: transparent;
		font-size: 14px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		color: #292930;
		padding: 3px 8px 4px;
		display: flex;
		margin-right: 8px;
		margin-bottom: 8px;
		border: 1px solid $color-red;
		transition: color .2s, background-color .2s;
		outline: none;
		&:hover {
			background-color: $color-red;
			color: $color-white;
		}
		&:active {
			background-color: $color-light-red;
		}
	}
	.link {
		margin-top: 4px;
	}
}