.svg {
	&-user {
		width: 10px;
		height: 12px;
		use {
			fill: $color-main2;
		}
	}
	&-logo {
		width: 168px;
		height: 40px;
	}
	&-search {
		width: 20px;
		height: 20px;
		use {
			fill: $color-white;
		}
	}
	&-arrow {
		width: 12px;
		height: 7.6px;
		use {
			fill: $color-white;
			stroke: $color-white;
		}
	}
	&-like {
		width: 18px;
		height: 16px;
		use {
			fill: $color-white;
			transition: fill .3s;
		}
	}
	&-shop {
		width: 16px;
		height: 14px;
		display: none;
		&--active {
			display: block;
		}
		use {
			fill: $color-red;
		}
	}
	&-sale {
		width: 44px;
		height: 44px;
	}
	&-prime {
		width: 60px;
		height: 48px;
		use {
			fill: $color-blue5e;
		}
	}
	&-social {
		width: 32px;
		height: 32px;
	}
	&-mmit {
		height: 22.5px;
		width: 90px;
	}
	&-close {
		width: 10px;
		height: 10px;
	}
	&-arrow_6 {
		width: 15px;
		height: 12px;
		use {
			transition: fill .3s;
		}
	}
	&-filter {
		width: 10px;
		height: 6px;
		use {
			fill: $color-main2;
		}
	}
	&-next {
		width: 10px;
		height: 6px;
		transform: rotate(-90deg);
		use {
			fill: $color-blue5e;
		}
	}
	&-prev {
		width: 10px;
		height: 6px;
		transform: rotate(90deg);
		use {
			fill: $color-blue5e;
		}
	}
	&-pas_1 {
		width: 18px;
		height: 15px;
		use {
			fill: #808086;
		}
	}
	&-pas_2 {
		width: 18px;
		height: 15px;
		use {
			fill: #808086;
		}
	}
	&-breadcrumbs {
		width: 9px;
		height: 5px;
		transform: rotate(-90deg);
		margin-right: 8px;
		margin-left: 8px;
		use {
			fill: rgba(45, 43, 35, 0.77);
		}
	}
}