.breadcrumbs {
	display: flex;
	margin-top: 22px;
	&__link {
		display: flex;
		align-items: center;
		font-size: 15px;
		color: rgba(45, 43, 35, 0.77);
		span {
			border-bottom: 1px solid rgba(45, 43, 35, 0.3);
			transition: border-bottom-color .3s;
		}
		&:hover span{
			border-bottom: 1px solid rgba(45, 43, 35, 0.6);
		}
	}
}