.modal {
	&__subtitle {
		font-family: $font-firaM;
		font-size: 22px;
		font-weight: 500;
		color: $color-txt1;
		text-align: left;
		margin-bottom: 22px;
	}
	&__block {
		display: flex;
		align-items: center;
		margin-bottom: 25px;
	}
	&__img {
		width: 67px;
		height: 67px;
		display: flex;
		justify-content: center;
		align-items: center;
		border: 1px solid rgba($color-main2, 0.3);
		margin-right: 24px;
		img {
			width: 100%;
			max-height: 67px;
		}
	}
	&__text {
		display: block;
		font-size: 17px;
		line-height: 1.18;
		color: $color-main2;
		margin-bottom: 5px;
	}
	&__size {
		font-size: 15px;
		font-style: italic;
		line-height: 1.33;
		color: $color-main2;
		margin-bottom: 5px;
	}
	&__price {
		font-size: 19px;
		line-height: 1.26;
		color: $color-main2;
	}
	&__min {
		font-family: $font-fira;
		font-size: 14px;
		font-style: italic;
		line-height: 1.43;
		color: #626260;
		margin-bottom: 18px;
		margin-top: 5px;
	}
	&__address-txt {
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		margin-bottom: 9px;
	}
	&__address-link {
		position: relative;
		border-radius: 3px;
		box-shadow: 0 1px 3px 0 rgba(182, 188, 198, 0.65);
		background-image: linear-gradient(to bottom, #ffffff, #f2f2f3);
		padding: 10px 33px 10px 16px;
		cursor: pointer;
		outline: none;
		min-width: 370px;
		&:hover {
			outline: none;
		}
		&:focus {
			outline: none;
		}
		&::before {
			content: '';
			position: absolute;
			top: 17px;
			right: 12px;
			width: 10px;
			height: 5.6px;
			background: url("../images/sprites/svg/arrow.svg") center no-repeat;
			-webkit-background-size: cover;
			background-size: cover;
		}
	}
	&__address {
		border-radius: 3px;
		box-shadow: 0 2px 6px 0 rgba(35, 35, 42, 0.26);
		background-color: #ffffff;
		border: 1px solid rgba($color-main2, 0.3);
		&-wrap {
			transition: max-height .6s;
			z-index: 2;
			width: 100%;
			position: absolute;
			top: 48px;
			left: 0;
			overflow: hidden;
			max-height: 0;
			&--active {
				max-height: 1000px;
			}
		}
	}
	&__par {
		margin-top: 22px;
		margin-bottom: 13px;
	}
	&__social {
		margin-bottom: 36px;
		svg {
			margin-right: 16px;
		}
	}
	&__option {
		position: relative;
		padding: 10px 9px 10px 12px;
		display: block;
		border-bottom: 1px solid rgba($color-main2, 0.3);
		cursor: pointer;
		&:hover {
			background: linear-gradient(to right, rgba(94, 77, 249, 0), rgba(57, 49, 133, 0.13));
		}
	}
	&__hidden {
		position: absolute;
		opacity: 0;
		z-index: -1;
	}
	&__item {
		&:last-child label {
			border-bottom: none;
		}
		&--active {
			.modal__option {
				background-color: #f4f4f4;
				&:hover {
					background: #f4f4f4;
				}
			}
		}
	}
	&__maps {
		width: 100%;
		overflow: hidden;
		max-height: 0;
		transition: max-height .3s;
		.modal__maps-wrap {
			margin-top: 11px;
			border: 1px solid rgba($color-main2, 0.3);
			border-radius: 3px;
		}
		&--active {
			max-height: 250px;
		}
	}
}
.quick-modal {
	min-width: 530px;
	.modal__form {
		width: 100%;
	}
	.modal__wrap {
		padding: 42px 32px 60px;
		align-items: start;
	}
	.modal__address-link {
		max-width: 370px;
	}
	.modal__label {
		span {
			text-align: left;
		}
	}
	.btn-red {
		max-width: 180px;
		margin-bottom: 21px;
	}
}