.title {
	font-family: $font-firaM;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
	color: $color-txt1;
	margin: 78px 0 36px;
}
.title--desc {
	margin-bottom: 11px;
}
.title-left {
	margin: 70px 0 36px;
	display: block;
	font-family: $font-firaM;
	font-size: 32px;
	font-weight: 500;
	color: $color-txt1;
}
.subtitle {
	margin: 60px 0 47px;
	font-family: $font-firaM;
	font-size: 34px;
	font-weight: 500;
	color: $color-txt1;
}
.desc {
	font-family: $font-fira;
	font-size: 20px;
	line-height: 1.4;
	text-align: center;
	color: $color-txt1;
	margin: 0;
}
.btn {
	border-radius: 3px;
	background-color: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	font-family: $font-fira;
	font-size: 15px;
	line-height: 1.6;
	text-align: center;
	color: $color-main2;
	padding: 4px 17px;
	cursor: pointer;
	transition: background-color .3s, color .3s, box-shadow .3s, border-color .3s;
	text-decoration: none;
}
.btn-red {
	box-shadow: 0 1px 4px 0 rgba($color-red, 0.63);
	background-color: $color-red;
	border-color: $color-red;
	font-size: 16px;
	line-height: 1.5;
	color: $color-white;
	padding-top: 12px;
	padding-bottom: 12px;
	transition: background-color .3s, box-shadow .3s;
	&:hover {
		background-color: $color-light-red;
	}
	&:active {
		box-shadow: inset 0 1px 2px 0 $color-dark-red;
		background-color: $color-red;
	}
}
.btn-reds {
	border-radius: 3px;
	box-shadow: 0 1px 4px 0 rgba(230, 72, 64, 0.63);
	background-color: $color-red;
	padding: 4px 16px;
	line-height: 1.6;
	color: $color-white;
	font-size: 15px;
	border: 1px solid $color-red;
	cursor: pointer;
	span {
		line-height: 1.6;
	}
	&:hover {
		background-color: $color-light-red;
	}
	&:active {
		box-shadow: inset 0 1px 2px 0 $color-dark-red;
		background-color: $color-red;
	}
}
.btn-disabled {
	opacity: 0.6;
}
.btn-red.btn-disabled:hover {
	background-color: $color-red
}
.link-solid-black {
	border-bottom: 1px solid rgba($color-main2, 0.3);
	transition: border-bottom-color .3s;
	&:hover{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link-solid-blue {
	border-bottom: 1px solid rgba($color-blue5e, 0.3);
	transition: border-bottom-color .3s;
	&:hover{
		border-bottom-color: rgba($color-blue5e, 0.6);
	}
}
.link-solid-blue-s {
	span {
		border-bottom: 1px solid rgba($color-blue5e, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-blue5e, 0.6);
	}
}
.link-solid-black-s {
	span {
		border-bottom: 1px solid rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link-dashed-s {
	border: none;
	font-family: $font-fira;
	cursor: pointer;
	background-color: transparent;
	span {
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link-dashed-blue-s {
	border: none;
	font-family: $font-fira;
	cursor: pointer;
	background-color: transparent;
	color: $color-blue5;
	span {
		border-bottom: 1px dashed rgba($color-blue5, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-blue5, 0.6);
	}
}
.link {
	display: inline-block;
	font-family: $font-fira;
	font-size: 14px;
	color: $color-main2;
	cursor: pointer;
	span {
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.box {
	margin-bottom: 23px;
}
.h3-title {
	font-family: $font-firaM;
	font-size: 14px;
	font-weight: 500;
	color: $color-main2;
	margin-bottom: 9px;
}
.par {
	max-width: 800px;
	font-family: $font-fira;
	font-size: 13px;
	line-height: 1.54;
	color: rgba($color-main2, 0.9);
}
.ul-items {

}
.ul-items__item {
	padding-left: 10px;
	font-size: 13px;
	line-height: 1.54;
	color: rgba($color-main2, 0.9);
	margin-top: 10px;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 3px;
		transform: translateY(-50%);
		width: 3px;
		height: 3px;
		background-color: rgba($color-main2, 0.9);
		border-radius: 50%;
	}
}
.to-favorites {
	position: absolute;
	top: 0;
	left: 0;
	width: 96px;
	text-align: center;
	padding: 4px 0;
	box-shadow: 0 1px 3px 0 rgba(61, 60, 72, 0.2);
	background-color: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	border-radius: 6px;
	font-size: 13px;
	color: rgba($color-main2, 0.8);
	z-index: 1;
	visibility: hidden;
	opacity: 0;
	transition: opacity .3s, visibility .3s;
	&::after {
		content: '';
		position: absolute;
		width: 14px;
		height: 14px;
		bottom: -7px;
		left: 50%;
		transform: translateX(-50%) rotate(45deg);
		background-color: $color-white;
		border: 1px solid rgba($color-main2, 0.3);
		z-index: -2;
	}
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $color-white;
		border-radius: 6px;
		z-index: -1;
	}
	&--active {
		z-index: 150;
		opacity: 1;
		visibility: visible;
	}
}
.in-favorites {
	position: absolute;
	top: 0;
	left: 0;
	width: 96px;
	text-align: center;
	padding: 4px 0;
	box-shadow: 0 1px 3px 0 rgba(61, 60, 72, 0.2);
	background-color: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	border-radius: 6px;
	font-size: 13px;
	color: rgba($color-main2, 0.8);
	visibility: hidden;
	opacity: 0;
	transition: opacity .3s, visibility .3s;
	&::after {
		content: '';
		position: absolute;
		width: 14px;
		height: 14px;
		bottom: -7px;
		left: 50%;
		transform: translateX(-50%) rotate(45deg);
		background-color: $color-white;
		border: 1px solid rgba($color-main2, 0.3);
		z-index: -2;
	}
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $color-white;
		border-radius: 6px;
		z-index: -1;
	}
	&--active {
		z-index: 150;
		opacity: 1;
		visibility: visible;
	}
}
.overlay-dark {
	&::before {
		content: '';
		position: fixed;
		width: 100vw;
		height: 100vh;
		top: 0;
		left: 0;
		z-index: 2;
		background-color: rgba($color-main2, 0.3);
		transition: background-color .3s;
	}
}