.yandex-title {
	display: block;
	font-family: $font-fira;
	font-size: 16px;
	line-height: 1.5;
	color: $color-main2;
	max-width: 194px;
	margin-bottom: 12px;
	span {
		border-bottom: 1px solid rgba($color-main2, 0.3);
		transition: border-bottom-color;
	}
	&:hover span {
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.yandex-phone {
	font-family: $font-fira;
	font-size: 16px;
	line-height: 1.5;
	color: $color-main2;
}
.yandex-time {
	font-family: $font-fira;
	font-size: 16px;
	font-style: italic;
	line-height: 1.5;
	color: rgba($color-main2, 0.8);
}
.map {
	border: 1px solid #d1cdbd;
	border-radius: 6px;
	box-sizing: border-box;
	overflow: hidden;
	.ymaps-2-1-72-balloon {
		box-shadow: 0 4px 16px 0 rgba(16,22,46,0.27) !important;
		border: none;
		border-radius: 6px;
		background-color: $color-white;
		padding: 0 !important;
	}
	.ymaps-2-1-72-balloon__layout {
		border-radius: 6px;
	}
	.ymaps-2-1-72-balloon__close-button {
		width: 10px !important;
		height: 10px !important;
		position: absolute;
		top: 10px;
		right: 10px;
	}
	.ymaps-2-1-72-balloon__content {
		padding: 19px 30px 24px 24px !important;
		margin-right: 0 !important;
		border-radius: 3px;
	}
	.yandex-img {
		position: absolute;
		bottom: -55px;
		left: 50%;
		transform: translateX(-50%);
	}
	.ymaps-2-1-72-balloon {
		left: auto !important;
		right: 0;
		top: auto !important;
		bottom: 55px;
		transform: translateX(50%);
	}
	.ymaps-2-1-72-balloon__layout {
		overflow: visible !important;
	}
	.ymaps-2-1-72-balloon__tail {
		left: 50% !important;
		margin-left: -11.5px;
	}
}
.modal__maps-wrap {
	.yandex-title {
		max-width: 500px;
		span {
			br {
				display: none;
			}
		}
	}
	.yandex-img {
		display: none;
	}
}