.toolbar {
	display: flex;
	margin-top: 27px;
	margin-bottom: 17px;
	align-items: center;
	&__box {
		display: flex;
		align-items: center;
	}
	&__check {
		font-size: 15px;
		line-height: 1.6;
		color: $color-main2;
		margin-right: 24px;
	}
	&__text {
		font-size: 15px;
		line-height: 1.6;
		color: $color-main2;
	}
	&__price {
		display: flex;
		align-items: center;
		margin-left: 14px;
		margin-right: 32px;
		svg {
			margin-left: 8px;
			use {
				fill: $color-blue5;
			}
		}
	}
	&__count {
		margin-left: 5px;
		margin-right: 24px;
	}
}