.top-menu {
	display: flex;
	&__box {
		display: flex;
		height: 56px;
		align-items: center;
	}
	&__box:first-child {
		width: 25%;
	}
	&__box:nth-child(2) {
		width: 60%;
		padding-left: 27px;
	}
	&__box:last-child {
		width: 15%;
		padding-left: 15px;
	}
	&__list {
		display: flex;
		height: 100%;
		li {
			height: 100%;
		}
	}
	&__tel {
		font-family: $font-firaM;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 56px;
		letter-spacing: 0.1px;
		margin-right: 17px;
		height: 100%;
	}
	&__link {
		display: block;
		height: 100%;
		padding-right: 9px;
		padding-left: 9px;
		line-height: 56px;
		span {
			border-bottom: 1px solid rgba($color-main2, 0.3);
			transition: border-bottom-color .3s;
		}
		&:hover {
			span {
				border-bottom-color: rgba($color-main2, 0.6);
			}
		}
	}
	&__user {
		height: 100%;
		line-height: 56px;
		span {
			margin-left: 10px;
			border-bottom: 1px dashed rgba($color-main2, 0.3);
			transition: border-bottom-color .3s;
		}
		&--active span{
			border-bottom: 1px solid rgba($color-main2, 0.3);
		}
		&:hover {
			span {
				border-bottom-color: rgba($color-main2, 0.6);
			}
		}
	}
}